<template>
    <!-- SM属性选择 -->
    <view class="box-in">
        <view class="box-in-in-3">
            <view :style="{
                'opacity': requestParam.sadomasochismAttrCode == '' ? '.5' : '1',
            }" class="box-in-in-3-title">
            SM属性-单选
            </view>
            <view class="box-in-in-3-list">
                <view class="box-in-in-3-list-item" :style="{
                    'border': item.code == requestParam.sadomasochismAttrCode ? '5rpx solid #333' : '',
                }" v-for="(item, index) in bodyData" :key="index"
                    @click="check(item.code)">
                    <view :style="{
                        'opacity': item.code == requestParam.sadomasochismAttrCode ? '1' : '.5',
                    }" class="box-in-in-3-list-item-text">{{ item.name }}</view>
                </view>
            </view>
        </view>
    </view>
</template>

<script>
export default {
data() {
    return {
        requestParam: {
            sadomasochismAttrCode: ""
        },
        bodyData: [
        ]
    }
},

mounted() {
    this.getBodyData();
},

methods:{
    open(){
        this.show = true;
    },
    close(){
        this.show = false;
    },
    check(sadomasochismAttrCode) {
        this.requestParam.sadomasochismAttrCode = sadomasochismAttrCode;
        this.$emit('change', sadomasochismAttrCode);
    },
    getBodyData() {
        this.$kt.request.request({
            api: this.$api.business.ovo.sadomasochismAttr.getList,
            stateSuccess: (res) => {
                this.bodyData = res.data;
            }
        });
    }
}
}
</script>

<style lang="scss" scoped>

.box-in {
text-align: center;
border-radius: 20rpx;

.box-in-in-3 {
    border-radius: 10rpx;
    padding: 10rpx;

    .box-in-in-3-title {
        font-size: 30rpx;
        color: #666;
        text-align: left;
        margin-bottom: 20rpx;
        // margin-left: 10rpx;
    }

    .box-in-in-3-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }

    .box-in-in-3-list-item {
        width: calc(33.33% - 20rpx);
        margin-bottom: 20rpx;
        border: 5rpx solid #f0f0f0;
        padding: 30rpx 10rpx 30rpx 10rpx;
        border-radius: 20rpx;
        text-align: center;
        box-sizing: border-box;
    }

    .box-in-in-3-list-item-text {
        font-size: 30rpx;
        color: #333;
        opacity: .5;
    }
}

}
</style>